import { BrowserRouter, Route, Switch } from "react-router-dom";
import Detail from "./components/Detail";
import Home from "./routers/Home";

const App = () => {
  return (
    <BrowserRouter>
      <Switch>
        <Route exact path="/">
          <Home />
        </Route>
        <Route path="/detail/:id" component={Detail} />

        {/* <Route path="/datial/:id">
          <Detail />
        </Route> */}
      </Switch>
    </BrowserRouter>
  );
};

export default App;
